* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: none;
    outline: none;
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
}

:root {
    --bg-color: #1f242d;
    --second-bg-color: #323946;
    --text-color: #fff;
    --main-color: #0ef;
}

html {
    font-size: 62.5%;
    overflow-x: hidden;
    overflow-y: auto;
}

body {
    background: var(--bg-color);
    color: var(--text-color);
     overflow-y: auto;
}

section {
  width: 100%;
  min-height: 100vh;
  padding: 10rem 9% 2rem;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 2rem 9%;
    background: var(--bg-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
}

.header.sticky {
    border-bottom: .1rem solid rgba(0, 0, 0, .2);
}

.logo {
    font-size: 2.5rem;
    color: var(--text-color);
    font-weight: 600;
    cursor: default;
}

.navbar a {
    font-size: 1.7rem;
    color: var(--text-color);
    margin-left: 4rem;
    transition: .3s;
}

.navbar a:hover {
    color: var(--main-color);
}

.navbar a.active {
    color: var(--main-color);
}


#menu-icon {
    font-size: 3.6rem;
    color: var(--text-color);
    display: none;
}

.home {
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-content h3 {
    font-size: 3.2rem;
    font-weight: 700;
}

.home-content h3 :nth-of-type(2) {
    margin-bottom: 2rem;
}

span {
    color: var(--main-color);
}

.home-content h1 {
    font-size: 5.6rem;
    font-weight: 700;
    line-height: 1.3;
}

.home-img img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid var(--main-color);
  box-shadow: 0 0 20px rgba(0, 238, 255, 0.4);
  animation: floatImage 4s ease-in-out infinite;
}
@keyframes floatImage {
    0%{
        transform: translateY(0)
    }
    50%{
        transform: translateY(-2.4rem)
    }
    100%{
        transform: translateY(0)
    }
}

.home-content p {
    font-size: 1.6rem;
}


.social-media a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    background: transparent;
    border: .2rem solid var(--main-color);
    border-radius: 50%;
    color: var(--main-color);
    margin: 3rem 1.5rem 3rem 0;
    transition: .5s ease;
}

.social-media a i {
    font-size: large;
}

.social-media a:hover {
    background: var(--main-color);
    color: var(--second-bg-color);
    box-shadow: 0 0 1rem var(--main-color);
}

.btn {
    display: inline-block;
    padding: 1rem 2.8rem;
    background: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color);
    font-size: 1.6rem;
    color: var(--second-bg-color);
    letter-spacing: .1rem;
    font-weight: 600;
    transition: .5s ease;
}

.btn:hover {
    box-shadow: none;
}

.about {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    padding: 5rem 3rem;
    background: var(--second-bg-color);
    flex-wrap: wrap;
}
/* .about {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    background: var(--second-bg-color);
} */

/* .about-img img {
    width: 40vw;
} */

.about-img img {
    width: 100%;
    max-width: 400px;
    border-radius: 15px;
    box-shadow: 0 0 30px rgba(0, 238, 255, 0.3);
    transition: transform 0.3s ease-in-out;
    object-fit: cover;
}

.about-img img:hover {
    transform: scale(1.03);
}


.heading {
    text-align: center;
    font-size: 5rem;

}

.about-content h2 {
    text-align: left;
    line-height: 1.5;
}

.about-content h3 {
    font-size: 2.6rem;
}

.about-content p {
    font-size: 1.6rem;
    margin: 2rem 0 3rem;
}


.services h2 {
    margin-bottom: 5rem;
}

.services-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
}

.services-container .services-box {
    flex: 1 1 30rem;
    background: var(--second-bg-color);
    padding: 3rem 2rem 4rem;
    border-radius: 2rem;
    text-align: center;
    border: .2rem solid var(--bg-color);
    transition: .5s ease;
}

.services-container .services-box:hover {
    border-color: var(--main-color);
    transform: scale(1.02);
}

.services-box i {
    font-size: 7rem;
    color: var(--main-color);
}

.services-box h3 {
    font-size: 2.6rem;
}

.services-box p {
    font-size: 1.6rem;
    margin: 1rem 0 3rem;
}


.portfolio {
    background: var(--second-bg-color);
}

.portfolio h2 {
    margin-bottom: 4rem;
}

.portfolio-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    gap: 2.5rem;
}

.portfolio-container .portfolio-box {
    position: relative;
    border-radius: 2rem;
    box-shadow: 0 0 1rem var(--bg-color);
    overflow: hidden;
    display: flex;
}

.portfolio-box img {
    width: 100%;
    transition: .5s ease;
}

.portfolio-box:hover img {
    transform: scale(1.1);
}

.portfolio-box .portfolio-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, .1), var(--main-color));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 4rem;
    transform: translateY(100%);
    transition: .5s ease;
}

.portfolio-box:hover .portfolio-layer {
    transform: translateY(0);
}

.portfolio-layer h4 {
    font-size: 3rem;
}

.portfolio-layer p {
    font-size: 1.6rem;
    margin: .3rem 0 1rem;
}

.portfolio-layer a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background: var(--text-color);
    border-radius: 50%;
}

.portfolio-layer a i {
    font-size: 2rem;
    color: var(--second-bg-color);
}

.contact h2 {
    margin-bottom: 3rem;
}

.contact form {
    max-width: 70rem;
    margin: 1rem auto;
    text-align: center;
    margin-bottom: 3rem;
}

.contact form .input-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.contact form .input-box input,
.contact form textarea {
    width: 100%;
    padding: 1.5rem;
    font-size: 1.6rem;
    color: var(--text-color);
    background: var(--second-bg-color);
    border-radius: .8rem;
    margin: .7rem 0;
}

.contact form .input-box input {
    width: 49%;
}

.contact FORM textarea {
    resize: none;
}

.contact form .btn {
    margin-top: 2rem;
    cursor: pointer;
}

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 2rem 9%;
    background: var(--second-bg-color);
}

.footer-text p {
    font-size: 1.6rem;
}

.footer-iconTap a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: .8rem;
    background: var(--main-color);
    border-radius: .8rem;
    transition: .5s ease;
}

.footer-iconTap a:hover {
    box-shadow: 0 0 1rem var(--main-color);
}

.footer-iconTap i {
    font-size: 2.4rem;
    color: var(--second-bg-color);
}



/* breakpoints */
/* 




/* ─── MOBILE OVERRIDES (screens ≤ 768px) ─── */

@media (max-width: 768px) {
  /* 1. Ensure body scrolls vertically only */
  body {
    overflow-x: hidden;
    overflow-y: auto;
  }

  /* 2. Stack home section, center contents */
  .home {
    flex-direction: column !important;
    align-items: center !important;
    text-align: center !important;
    padding-top: 8rem !important;
  }

  .home-content {
    width: 100% !important;
    max-width: 90% !important;
  }

  /* 3. Scale down headings */
  .home-content h1 {
    font-size: 3.2rem !important;
    line-height: 1.2 !important;
  }
  .home-content h3 {
    font-size: 2.2rem !important;
  }
  .home-content p {
    font-size: 1.4rem !important;
    margin: 1rem 0 !important;
  }

  /* 4. Center & shrink the profile image */
  .home-img {
    margin-top: 2rem !important;
  }
  .home-img img {
    width: 70vw !important;
    max-width: 250px !important;
    height: auto !important;
  }

  /* 5. Center social icons & tighten spacing */
  .social-media {
    display: flex !important;
    justify-content: center !important;
    gap: 1rem !important;
    margin: 1.5rem 0 !important;
  }
  .social-media a {
    width: 3.5rem !important;
    height: 3.5rem !important;
    font-size: 1.6rem !important;
  }

  /* 6. Shrink the download button */
  .btn {
    padding: 0.8rem 2rem !important;
    font-size: 1.4rem !important;
  }

  /* 7. Stack and center the about section */
  .about {
    flex-direction: column !important;
    text-align: center !important;
    padding: 5rem 2rem !important;
  }
  .about-img img {
    width: 80% !important;
    max-width: 300px !important;
    margin: 2rem auto !important;
  }
  .about-content h2,
  .about-content h3 {
    font-size: 2.4rem !important;
  }
  .about-content p {
    font-size: 1.4rem !important;
  }

  /* 8. Services stack vertically */
  .services-container {
    flex-direction: column !important;
    gap: 2rem !important;
    padding: 2rem 1rem !important;
  }
  .services-box h3 {
    font-size: 2rem !important;
  }
  .services-box p {
    font-size: 1.4rem !important;
  }

  /* 9. Portfolio — single column grid */
  .portfolio-container {
    grid-template-columns: 1fr !important;
    padding: 0 1rem !important;
  }
  .portfolio-layer h4 {
    font-size: 2.2rem !important;
  }
  .portfolio-layer p {
    font-size: 1.4rem !important;
  }

  /* 10. Contact form — full width inputs */
  .contact form .input-box {
    flex-direction: column !important;
    gap: 1rem !important;
  }
  .contact form .input-box input {
    width: 100% !important;
  }
  .contact form textarea {
    width: 100% !important;
  }

  /* 11. Footer — stack & center */
  .footer {
    flex-direction: column-reverse !important;
    text-align: center !important;
    gap: 1rem !important;
    padding: 2rem 2rem !important;
  }
  .footer-text p {
    font-size: 1.4rem !important;
  }
}


#menu-icon {
  font-size: 3.6rem;
  color: var(--text-color);
  display: none;
}

@media (max-width: 768px) {
  #menu-icon {
    display: block;
    cursor: pointer;
  }
}
.navbar {
  display: flex;
}

@media (max-width: 768px) {
  .navbar {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: var(--bg-color);
    flex-direction: column;
    align-items: center;
    display: none; /* hidden by default */
  }

  .navbar.active {
    display: flex;
  }

  .navbar a {
    margin: 1.5rem 0;
    font-size: 2rem;
  }
}